body {
    background: url("https://newevolutiondesigns.com/images/freebies/city-iphone-wallpaper-19.jpg") no-repeat center center fixed;
    background-size: cover;
    width: 100%;
    text-align: center;
}
.header {
    font-family: 'Yellowtail', cursive;
    color: white;
    font-size: 4rem;
    margin-top: 75%;
}

#filterDeals {
    display:inline-block;
    
    margin: 30% auto 0 auto;
    #locationSelector {
        border: none;
        border-radius: 1rem;
        margin: 0 auto 0 auto;
        width: 80%;
        height: 3rem;
        font-size: 1.6rem;
        text-align: center;
        background-color: #92aba7f0;
        color: white;
    }

    #dayOfWeekSelector {
        margin: 3rem auto 0 auto;
        a {
            font-size: 1.6rem;
            border-radius: 2rem;
            background-color: #f0dd9beb;
            border: none;
            color: #270703;
            padding: 1rem 2.5rem 1rem 2.5rem;
        }
            
    }
}

#card {
    h5 {
        font-weight: bold;
    }
    a {
        color:  white;
    }
    background-color: white;
    border-radius: 1rem;
    margin: 2rem auto;
    box-shadow: 0  5px 10px rgba(154,160,185,0.05),0 15px 40px rgba(166,173,201,0.2);
    width: 90%;
    height: 100%;
    text-align: left !important;
    padding: .5rem 0 .5rem 2rem;
    .info {
        display: inline-block;
        width: 70%;
       .description {
        font-family: 'Abril Fatface', cursive;
        padding-top: 1rem;
        }
        .location {
            color:white;
            font-weight: 200;
            font-family: 'Comfortaa', cursive;
            padding: .4rem 0 .4rem 0;
            max-width: 60%;
            font-size: .9rem;
            background-color: rgb(185, 185, 185);
            text-align: center;
            border-radius: 5px;
            margin-bottom: 1.5rem;
            vertical-align: middle;
        }
        .address {

        }
    }
    
    .directions {
        display: inline-block;
        font-size: 3rem;
        margin: auto 0 auto 1rem;
        padding: auto 0 auto 0;
        width: 15%;
        i {
            margin: 50% 0 50% 0;
            float: right;
            color: #74a6e1;
        }
    }
    .companyName {
        font-family: 'Comfortaa', cursive;
        
        font-size: 1.25rem;
    }
}